.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*::-webkit-input-placeholder {
  color: #666;
  opacity: 1;
}

*:-moz-placeholder {
  color: #666;
  opacity: 1;
}

*::-moz-placeholder {
  color: #666;
  opacity: 1;
}

*:-ms-input-placeholder {
  color: #666;
  opacity: 1;
}

*|*:any-link {
  cursor: pointer;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border: 3px solid red;
}

body input:required:valid,
body textarea:required:valid {
  border: 3px solid green;
}

html, body {
  height: 100%;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.75;
  font-family: "roboto-example", sans-serif;
  overflow-x: hidden;
  opacity: 1;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
  text-transform: uppercase;
  line-height: 1.4;
}

ul {
  list-style-type: none;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.button {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: orange;
  padding: 10px 35px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 3px;
  border-radius: 2px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .1s ease;
}

.button::after {
  transition: background-color .2s ease;
  position: absolute;
  content: '';
  height: 4px;
  bottom: 0;
  width: 100%;
  background-color: black;
  opacity: .18;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  left: 0;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: #ffae1a;
}

.button:hover::after {
  opacity: .22;
}

.button:active {
  background-color: #e69500;
}

.button:active::after {
  opacity: .32;
}

.small-btn {
  padding: 5px 17px;
}

.checkbox-styled {
  display: none !important;
}

.checkbox-styled + label:before {
  font-family: FontAwesome;
  display: inline-block;
}

.checkbox-styled + label:before {
  content: "\f096";
}

.checkbox-styled + label:before {
  letter-spacing: 10px;
}

.checkbox-styled:checked + label:before {
  content: "\f046";
}

.checkbox-styled:checked + label:before {
  letter-spacing: 5px;
}

.table {
  display: table;
  table-layout: fixed;
}

.table-row {
  display: table-row;
}

.table-cell {
  display: table-cell;
  vertical-align: middle;
}

.list-item {
  display: list-item !important;
}

.text-center {
  text-align: center;
}

.horizontal-list {
  padding: 0;
  margin: 0;
}

.horizontal-list li {
  display: inline-block;
}

.horizontal-list li a {
  display: block;
  padding: 10px;
  text-decoration: none;
}

.active-tab {
  border-bottom: 3px solid black;
}

.top_header {
  padding: 7px 0;
  background-color: #ccc;
}

.top_menu {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  text-align: center;
}

.top_menu li {
  display: inline-block;
  padding: 0;
  margin: 0;
}

.top_menu li a {
  text-decoration: none;
  display: block;
  padding: 10px;
  background-color: #ccc;
  color: #3A3A3A;
  font-family: RobotoCondensedRegular;
  transition: all .25s;
}

.top_menu li a:hover {
  background-color: #bcbcbc;
}

.top_right {
  padding: 0;
  margin: 0;
  float: right;
}

.top_right li {
  padding: 9px;
  display: inline-block;
}

.top_right .top-user-toggler a {
  display: block;
  color: #000;
}

.top_right .top-user-toggler a:hover, .top_right .top-user-toggler a:focus {
  text-decoration: none;
}

.search_panel_button {
  margin: auto;
}

.logo {
  float: left;
}

.logo img {
  width: 100px;
}

.top_menu_button {
  background-color: transparent;
  border: none;
  font-size: 34px;
  vertical-align: middle;
}

.search_panel ul {
  padding: 0;
  margin: 0;
  text-align: center;
}

.search_panel ul li {
  display: inline-block;
}

.search_panel ul li a {
  text-decoration: none;
  display: block;
  padding: 10px;
}

.search_bar {
  margin-top: 20px;
}

.search_bar ul {
  margin-bottom: 20px;
}

.search_bar ul li {
  color: #000;
  padding: 10px;
}

.search_bar ul li:hover {
  border-bottom: 3px solid black;
}

.search_form form {
  border: 1px solid black;
  position: relative;
}

.search_form form input {
  width: 100%;
  border: none;
  padding: .5rem 7.5rem .5rem 3rem;
}

.search_form form button {
  position: absolute;
  top: .2rem;
  right: .2rem;
}

.search_form form i {
  top: 1rem;
  left: 1rem;
  position: absolute;
}

.form-control {
  display: block;
  width: 100%;
}

.top-user-menu {
  display: none;
  z-index: 10;
  padding: 0;
  background-color: #ccc;
  position: absolute;
  right: 0;
  margin-top: 25px;
  width: 280px;
}

.top-user-menu .user-header {
  text-align: center;
}

.top-user-menu .user-body {
  background-color: #dddddd;
}

.top-user-menu .user-body a {
  color: #000;
}

.top-user-menu .user-body a:hover {
  text-decoration: none;
}

.top-user-menu .user-footer {
  background-color: #e7e7e7;
}

.top-user-menu .user-footer a {
  background-color: #b2b2b2;
  border: 1px solid #9B9B9B;
  color: #000;
}

.top-user-menu .user-footer a:hover {
  background-color: #9d9d9d;
  border: 1px solid #808080;
  text-decoration: none;
}

.search_result {
  background-color: #efefef;
  padding: 15px;
  margin: 20px 0px 20px 0px;
}

.search_result .search_result_pages {
  float: right;
}

.search_result .search_result_pages a {
  border-bottom: 2px solid transparent;
  margin-right: 2rem;
  display: inline-block;
  padding: 5px 5px 2px 5px;
}

.search_result .search_result_pages a:hover {
  text-decoration: none;
  border-bottom: 2px solid black;
}

.article_container {
  padding: 15px;
}

.article_container .article_item {
  background-color: #efefef;
  padding: 10px;
  margin-bottom: 30px;
}

.article_container .article_item h3 {
  line-height: 1.2;
}

.article_container .article_item .article_container_stats p {
  line-height: 1;
}

.article_container .article_item .article_container_stats .stats_text {
  font-size: 10px;
}

.center {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}

#articles-table a {
  cursor: pointer;
}

.sidebar-menu {
  margin: 0;
  padding: 0;
}

.sidebar-menu li {
  position: relative;
  margin: 0;
  padding: 0;
}

.sidebar-menu li a {
  padding: 12px 5px 12px 15px;
  display: block;
}

.sidebar-menu li a i {
  width: 20px;
}

.active_folder {
  color: #000 !important;
}

.panel-folders > ul {
  padding: 0;
  margin: 0;
}

#contextMenu {
  position: absolute;
  display: none;
}

#contextMenu ul {
  margin: 2px 0px 0px 0px;
  padding: 5px 0px;
  border-radius: 4px;
  background-color: #ccc;
  font-size: 14px;
  min-width: 160px;
}

#contextMenu ul li a {
  display: block;
  padding: 3px 20px;
}

#contextMenu ul li a:hover {
  text-decoration: none;
  background-color: #f5f5f5;
}

.central_toolbar ul {
  margin: 0;
  padding: 0;
}

.central_toolbar .controller_toolbar {
  float: left;
}

.central_toolbar .sort_toolbar {
  float: right;
}

.article_selected {
  background-color: #ccc;
}

#articles_list h2 {
  margin: 0;
  line-height: 1.2;
  height: 20px;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 90px;
}

#articles_list p {
  font-size: 12px;
  margin: 0;
  line-height: 1.1;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 18px;
  white-space: nowrap;
  margin-right: 90px;
}

#articles_list ul {
  margin: 0;
  padding: 0;
}

#articles_list ul li {
  padding: 10px;
  padding-left: 120px;
  position: relative;
}

#articles_list .check_indicator, #articles_list .favorite_indicator, #articles_list .read_indicator, #articles_list .file_indicator {
  text-align: center;
  width: 32px;
  position: absolute;
  line-height: 54px;
  top: 0;
  z-index: 10;
}

#articles_list .check_indicator {
  left: 0;
}

#articles_list .check_indicator input {
  z-index: 10;
}

#articles_list .check_indicator label {
  pointer-events: none;
}

#articles_list .favorite_indicator {
  color: #BBBBBB;
  left: 32px;
  transition: color 0.25s ease;
}

#articles_list .favorite_indicator:hover {
  color: #8D01A2;
}

#articles_list .favorite_indicator_active {
  color: #8D01A2;
}

#articles_list .read_indicator {
  left: 64px;
}

#articles_list .file_indicator {
  left: 80px;
}

#articles_list .date_added {
  position: absolute;
  right: 32px;
  top: 0;
  line-height: 54px;
}

#related_tabs {
  display: none;
}

#related_tabs > ul {
  margin: 0;
  padding: 0;
  font-size: 0;
}

#related_tabs > ul li {
  width: 50%;
  display: inline-block;
  position: relative;
  text-align: center;
  border-bottom: 1px solid #000;
}

#related_tabs > ul li a {
  font-size: 14px;
  color: #000;
  display: block;
}

#related_tabs > ul li a:hover, #related_tabs > ul li a:focus {
  text-decoration: none;
}

.slidingTabs {
  position: relative;
  overflow-x: hidden;
}

.slidingTabs ul {
  padding: 0;
}

.slidingTabs p {
  margin: 0;
}

.slidingTabs .article_title {
  margin: 0;
}

.slidingTabs .article_abstract {
  max-height: 145px;
  overflow: hidden;
  position: relative;
  word-wrap: break-word;
  white-space: normal;
}

.slidingTabs .article_abstract:before {
  content: '';
  height: 65px;
  display: block;
  position: absolute;
  bottom: 0;
  left: -20px;
  right: -20px;
  box-shadow: inset 0 -45px 55px -10px #F6F6F6;
  pointer-events: none;
}

.slidingTabs .article_abstract_expanded {
  max-height: none;
}

.slidingTabs .article_abstract_expanded:before {
  content: none;
}

.abstract_less_toggle {
  display: none;
}

.slidingTabs .tab {
  width: 100%;
  display: inline-block;
  position: relative;
}

.slidingTabs .tab:nth-child(2) {
  position: absolute;
  top: 0;
  left: 100%;
}

.edit_article_tool {
  position: absolute;
  right: 0;
  top: 10px;
}

.edit_article_tool a {
  padding: 5px;
}

.edit_article_info {
  display: none;
  padding-top: 50px;
}

.sidebar_upload {
  text-align: center;
}

.sidebar_upload #zone10 {
  background-color: #ccc;
  padding: 10px;
}

.sidebar_upload .progress span {
  animation: progress .7s linear infinite;
  background: url("../img/progress.gif");
  display: block;
  height: 1em;
  width: 0;
}

@keyframes progress {
  from {
    background-position: left top;
  }
  to {
    background-position: 30px top;
  }
}

.fd-zone {
  position: relative;
  overflow: hidden;
  /* The following are not required but create a pretty box: */
  width: 15em;
  margin: 0 auto;
  text-align: center;
}

/* Hides <input type="file"> while simulating "Browse" button: */
.fd-file {
  opacity: 0;
  font-size: 118px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
  padding: 0;
  margin: 0;
  cursor: pointer;
  filter: alpha(opacity=0);
  font-family: sans-serif;
}

.bottom_fixed_instrument {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 100px;
  width: 100%;
  background-color: #d5d5d5;
}

